'use client'

import { useState } from 'react'
import Sidebar from '@/components/sidebar1'
import Header from '@/components/header'
import LinesPage from '@/components/pages/lines-page'
import StopsPage from '@/components/pages/stops-page'

export default function Home() {
  const [currentCity, setCurrentCity] = useState('Agadir')
  const [currentPage, setCurrentPage] = useState('lines')

  return (
    <div className="flex h-screen bg-background">
      {/* Sidebar */}
      <Sidebar currentPage={currentPage} setCurrentPage={setCurrentPage} />
      
      {/* Main Content */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Header */}
        <Header currentCity={currentCity} setCurrentCity={setCurrentCity} currentPage={currentPage} />
        
        {/* Page Content */}
        <main className="flex-1 overflow-auto">
          {currentPage === 'lines' && <LinesPage currentCity={currentCity} />}
          {currentPage === 'stops' && <StopsPage currentCity={currentCity} />}
        </main>
      </div>
    </div>
  )
}
