'use client'

import { useState, useEffect } from 'react'
import { X } from 'lucide-react'
import {Button} from '@/components/ui/button'

interface Stop {
  id: string
  name: string
  code: string
  address: string
  latitude: number
  longitude: number
  accessibility: boolean
  status: 'active' | 'inactive'
  associatedLines: string[]
}

interface StopFormProps {
  city: string
  stop?: Stop | null
  onSave: (stop: {
    name: string
    code: string
    address: string
    latitude: number
    longitude: number
    accessibility: boolean
  }) => void
  onCancel: () => void
}

export default function StopFormModal({ city, stop, onSave, onCancel }: StopFormProps) {
  const [formData, setFormData] = useState({
    name: '',
    code: '',
    address: '',
    latitude: '',
    longitude: '',
    accessibility: false,
  })

  const [errors, setErrors] = useState<Record<string, string>>({})

  // Initialize form with stop data if editing
  useEffect(() => {
    if (stop) {
      setFormData({
        name: stop.name,
        code: stop.code,
        address: stop.address,
        latitude: stop.latitude.toString(),
        longitude: stop.longitude.toString(),
        accessibility: stop.accessibility,
      })
    }
  }, [stop])

  const validateForm = () => {
    const newErrors: Record<string, string> = {}

    if (!formData.name.trim()) newErrors.name = 'Le nom est requis'
    if (!formData.address.trim()) newErrors.address = 'L\'adresse est requise'
    if (!formData.latitude) newErrors.latitude = 'La latitude est requise'
    if (!formData.longitude) newErrors.longitude = 'La longitude est requise'

    const lat = parseFloat(formData.latitude)
    const lng = parseFloat(formData.longitude)

    if (isNaN(lat) || lat < -90 || lat > 90) {
      newErrors.latitude = 'Latitude invalide (-90 à 90)'
    }
    if (isNaN(lng) || lng < -180 || lng > 180) {
      newErrors.longitude = 'Longitude invalide (-180 à 180)'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()

    if (!validateForm()) return

    onSave({
      name: formData.name,
      code: formData.code,
      address: formData.address,
      latitude: parseFloat(formData.latitude),
      longitude: parseFloat(formData.longitude),
      accessibility: formData.accessibility,
    })
  }

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <div className="bg-card rounded-lg shadow-xl max-w-2xl w-full">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-border">
          <h2 className="text-xl font-bold text-foreground">
            {stop ? 'Modifier l\'arrêt' : 'Créer un nouvel arrêt'}
          </h2>
          <button
            onClick={onCancel}
            className="p-2 hover:bg-muted rounded-lg transition-colors"
          >
            <X className="w-5 h-5 text-foreground" />
          </button>
        </div>

        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          {/* Basic Info */}
          <div className="space-y-4">
            <h3 className="font-semibold text-foreground">Informations Principales</h3>

            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Nom de l'arrêt *
                </label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  placeholder="ex: Gare Routière"
                  className={`w-full px-3 py-2 rounded-lg border ${
                    errors.name ? 'border-destructive' : 'border-input'
                  } bg-background text-foreground`}
                />
                {errors.name && <p className="text-xs text-destructive mt-1">{errors.name}</p>}
              </div>
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Code
                </label>
                <input
                  type="text"
                  value={formData.code}
                  onChange={(e) => setFormData({ ...formData, code: e.target.value })}
                  placeholder="ex: GR01"
                  className="w-full px-3 py-2 rounded-lg border border-input bg-background text-foreground"
                />
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-foreground mb-2">
                Adresse *
              </label>
              <input
                type="text"
                value={formData.address}
                onChange={(e) => setFormData({ ...formData, address: e.target.value })}
                placeholder="ex: Avenue Hassan II, Agadir"
                className={`w-full px-3 py-2 rounded-lg border ${
                  errors.address ? 'border-destructive' : 'border-input'
                } bg-background text-foreground`}
              />
              {errors.address && <p className="text-xs text-destructive mt-1">{errors.address}</p>}
            </div>
          </div>

          {/* Coordinates */}
          <div className="space-y-4">
            <h3 className="font-semibold text-foreground">Géolocalisation</h3>

            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Latitude *
                </label>
                <input
                  type="number"
                  step="0.0001"
                  value={formData.latitude}
                  onChange={(e) => setFormData({ ...formData, latitude: e.target.value })}
                  placeholder="30.4202"
                  className={`w-full px-3 py-2 rounded-lg border ${
                    errors.latitude ? 'border-destructive' : 'border-input'
                  } bg-background text-foreground`}
                />
                {errors.latitude && <p className="text-xs text-destructive mt-1">{errors.latitude}</p>}
              </div>
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Longitude *
                </label>
                <input
                  type="number"
                  step="0.0001"
                  value={formData.longitude}
                  onChange={(e) => setFormData({ ...formData, longitude: e.target.value })}
                  placeholder="-9.5981"
                  className={`w-full px-3 py-2 rounded-lg border ${
                    errors.longitude ? 'border-destructive' : 'border-input'
                  } bg-background text-foreground`}
                />
                {errors.longitude && <p className="text-xs text-destructive mt-1">{errors.longitude}</p>}
              </div>
            </div>
          </div>

          {/* Accessibility */}
          <div className="space-y-4">
            <h3 className="font-semibold text-foreground">Accessibilité</h3>
            <label className="flex items-center gap-3 cursor-pointer">
              <input
                type="checkbox"
                checked={formData.accessibility}
                onChange={(e) => setFormData({ ...formData, accessibility: e.target.checked })}
                className="w-4 h-4 rounded border-input"
              />
              <span className="text-sm text-foreground">Accessible aux personnes à mobilité réduite</span>
            </label>
          </div>

          {/* Form Actions */}
          <div className="flex gap-3 justify-end pt-6 border-t border-border">
            <Button type="button" onClick={onCancel} variant="outline">
              Annuler
            </Button>
            <Button type="submit" className="bg-primary text-primary-foreground hover:opacity-90">
              {stop ? 'Mettre à jour l\'arrêt' : 'Sauvegarder l\'arrêt'}
            </Button>
          </div>
        </form>
      </div>
    </div>
  )
}
