'use client'

import { useState } from 'react'
import { Plus, Edit2, Trash2, Clock, Accessibility } from 'lucide-react'
import StopsTable from '@/components/stops-table'
import StopFormModal from '@/components/stop-form-modal'
import {Button} from '@/components/ui/button'

interface Stop {
  id: string
  name: string
  code: string
  address: string
  latitude: number
  longitude: number
  accessibility: boolean
  status: 'active' | 'inactive'
  associatedLines: string[]
}

interface StopsPageProps {
  currentCity: string
}

export default function StopsPage({ currentCity }: StopsPageProps) {
  const [stops, setStops] = useState<Stop[]>([
    {
      id: '1',
      name: 'Gare Routière',
      code: 'GR01',
      address: 'Avenue Hassan II, Agadir',
      latitude: 30.4202,
      longitude: -9.5981,
      accessibility: true,
      status: 'active',
      associatedLines: ['L01', 'L03'],
    },
    {
      id: '2',
      name: 'Centre Ville',
      code: 'CV01',
      address: 'Place Sidi Mohamed Ben Abdellah, Agadir',
      latitude: 30.4244,
      longitude: -9.5957,
      accessibility: true,
      status: 'active',
      associatedLines: ['L02'],
    },
    {
      id: '3',
      name: 'Plage d\'Agadir',
      code: 'PA01',
      address: 'Boulevard de la Corniche, Agadir',
      latitude: 30.4283,
      longitude: -9.6024,
      accessibility: false,
      status: 'active',
      associatedLines: ['L02'],
    },
    {
      id: '4',
      name: 'Tiklouine',
      code: 'TK01',
      address: 'Route de Tiklouine, Agadir',
      latitude: 30.4120,
      longitude: -9.6100,
      accessibility: false,
      status: 'active',
      associatedLines: ['L01'],
    },
    {
      id: '5',
      name: 'Aït Melloul',
      code: 'AM01',
      address: 'Avenue Principale, Aït Melloul',
      latitude: 30.3850,
      longitude: -9.6200,
      accessibility: true,
      status: 'inactive',
      associatedLines: ['L03'],
    },
  ])

  const [showForm, setShowForm] = useState(false)
  const [editingStop, setEditingStop] = useState<Stop | null>(null)
  const [searchTerm, setSearchTerm] = useState('')
  const [filterAccessibility, setFilterAccessibility] = useState<'all' | 'accessible'>('all')

  const filteredStops = stops.filter(stop => {
    const matchesSearch = 
      stop.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      stop.code.toLowerCase().includes(searchTerm.toLowerCase()) ||
      stop.address.toLowerCase().includes(searchTerm.toLowerCase())
    
    const matchesAccessibility = filterAccessibility === 'all' || (filterAccessibility === 'accessible' && stop.accessibility)
    
    return matchesSearch && matchesAccessibility
  })

  const handleSaveStop = (stopData: Omit<Stop, 'id' | 'status' | 'associatedLines'>) => {
    if (editingStop) {
      setStops(stops.map(stop =>
        stop.id === editingStop.id
          ? { ...stop, ...stopData }
          : stop
      ))
      setEditingStop(null)
    } else {
      const stop: Stop = {
        ...stopData,
        id: Date.now().toString(),
        status: 'active',
        associatedLines: [],
      }
      setStops([...stops, stop])
    }
    setShowForm(false)
  }

  const handleDeleteStop = (id: string) => {
    if (window.confirm('Êtes-vous sûr de vouloir supprimer cet arrêt?')) {
      setStops(stops.filter(stop => stop.id !== id))
    }
  }

  const handleToggleStatus = (id: string) => {
    setStops(stops.map(stop =>
      stop.id === id
        ? { ...stop, status: stop.status === 'active' ? 'inactive' : 'active' }
        : stop
    ))
  }

  const handleEditStop = (stop: Stop) => {
    setEditingStop(stop)
    setShowForm(true)
  }

  const handleCloseForm = () => {
    setShowForm(false)
    setEditingStop(null)
  }

  return (
    <div className="p-6">
      {/* Top Section */}
      <div className="mb-6">
        <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4 mb-4">
          <div className="flex-1 flex gap-4">
            <input
              type="text"
              placeholder="Rechercher un arrêt..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="flex-1 px-4 py-2 rounded-lg border border-input bg-card text-foreground placeholder-muted-foreground"
            />
            <select
              value={filterAccessibility}
              onChange={(e) => setFilterAccessibility(e.target.value as 'all' | 'accessible')}
              className="px-4 py-2 rounded-lg border border-input bg-card text-foreground text-sm"
            >
              <option value="all">Tous les arrêts</option>
              <option value="accessible">Accessibles PMR</option>
            </select>
          </div>
          <Button onClick={() => setShowForm(true)} className="bg-primary text-primary-foreground hover:opacity-90 whitespace-nowrap">
            <Plus className="w-4 h-4 mr-2" />
            Ajouter un arrêt
          </Button>
        </div>

        {/* Results Summary */}
        <div className="text-sm text-muted-foreground">
          {filteredStops.length} arrêt{filteredStops.length !== 1 ? 's' : ''} trouvé{filteredStops.length !== 1 ? 's' : ''}
        </div>
      </div>

      {/* Form Modal */}
      {showForm && (
        <StopFormModal
          city={currentCity}
          stop={editingStop}
          onSave={handleSaveStop}
          onCancel={handleCloseForm}
        />
      )}

      {/* Table */}
      {filteredStops.length > 0 ? (
        <StopsTable
          stops={filteredStops}
          onEdit={handleEditStop}
          onDelete={handleDeleteStop}
          onToggleStatus={handleToggleStatus}
        />
      ) : (
        <div className="flex items-center justify-center py-12 bg-card rounded-lg border border-border">
          <div className="text-center">
            <Clock className="w-8 h-8 text-muted-foreground mx-auto mb-2" />
            <p className="text-muted-foreground">Aucun arrêt trouvé</p>
          </div>
        </div>
      )}
    </div>
  )
}
