'use client'

import { useState } from 'react'
import { Plus, Edit2, Trash2, Clock } from 'lucide-react'
import LineCard from '@/components/line-card'
import LineFormModal from '@/components/line-form-modal'
import {Button} from '@/components/ui/button'

interface Line {
  id: string
  number: string
  name: string
  city: string
  stops: number
  direction: string
  firstStop: string
  lastStop: string
  status: 'active' | 'inactive'
}

interface LinesPageProps {
  currentCity: string
}

export default function LinesPage({ currentCity }: LinesPageProps) {
  const [lines, setLines] = useState<Line[]>([
    {
      id: '1',
      number: 'L01',
      name: 'Agadir Centre Ville',
      city: 'Agadir',
      stops: 5,
      direction: 'Agadir Centre Ville → Tiklouine',
      firstStop: 'Gare Routière',
      lastStop: 'Tiklouine',
      status: 'active',
    },
    {
      id: '2',
      number: 'L02',
      name: 'Agadir Plage',
      city: 'Agadir',
      stops: 8,
      direction: 'Centre → Plage Agadir',
      firstStop: 'Centre Ville',
      lastStop: 'Plage d\'Agadir',
      status: 'active',
    },
    {
      id: '3',
      number: 'L03',
      name: 'Agadir Aït Melloul',
      city: 'Agadir',
      stops: 6,
      direction: 'Agadir → Aït Melloul',
      firstStop: 'Gare Routière',
      lastStop: 'Aït Melloul',
      status: 'inactive',
    },
  ])

  const [showForm, setShowForm] = useState(false)
  const [editingLine, setEditingLine] = useState<Line | null>(null)
  const [searchTerm, setSearchTerm] = useState('')

  const filteredLines = lines.filter(line => 
    line.city === currentCity &&
    (line.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
     line.number.toLowerCase().includes(searchTerm.toLowerCase()))
  )

  const toggleLineStatus = (id: string) => {
    setLines(lines.map(line =>
      line.id === id
        ? { ...line, status: line.status === 'active' ? 'inactive' : 'active' }
        : line
    ))
  }

  const handleDeleteLine = (id: string) => {
    if (window.confirm('Êtes-vous sûr de vouloir supprimer cette ligne?')) {
      setLines(lines.filter(line => line.id !== id))
    }
  }

  const handleSaveLine = (lineData: Omit<Line, 'id' | 'status'>) => {
    if (editingLine) {
      setLines(lines.map(line =>
        line.id === editingLine.id
          ? { ...line, ...lineData }
          : line
      ))
      setEditingLine(null)
    } else {
      const line: Line = {
        ...lineData,
        id: Date.now().toString(),
        status: 'active',
      }
      setLines([...lines, line])
    }
    setShowForm(false)
  }

  const handleEditLine = (line: Line) => {
    setEditingLine(line)
    setShowForm(true)
  }

  const handleCloseForm = () => {
    setShowForm(false)
    setEditingLine(null)
  }

  return (
    <div className="p-6">
      {/* Top Actions */}
      <div className="flex items-center justify-between mb-6">
        <div className="flex-1 max-w-md">
          <input
            type="text"
            placeholder="Rechercher une ligne..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full px-4 py-2 rounded-lg border border-input bg-card text-foreground placeholder-muted-foreground"
          />
        </div>
        <Button onClick={() => setShowForm(true)} className="bg-primary text-primary-foreground hover:opacity-90">
          <Plus className="w-4 h-4 mr-2" />
          Ajouter une ligne
        </Button>
      </div>

      {/* Form Modal */}
      {showForm && (
        <LineFormModal
          city={currentCity}
          line={editingLine}
          onSave={handleSaveLine}
          onCancel={handleCloseForm}
        />
      )}

      {/* Lines Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {filteredLines.length > 0 ? (
          filteredLines.map(line => (
            <LineCard
              key={line.id}
              line={line}
              onToggleStatus={() => toggleLineStatus(line.id)}
              onEdit={() => handleEditLine(line)}
              onDelete={() => handleDeleteLine(line.id)}
            />
          ))
        ) : (
          <div className="col-span-full flex items-center justify-center py-12 text-muted-foreground">
            <Clock className="w-5 h-5 mr-2" />
            Aucune ligne trouvée
          </div>
        )}
      </div>
    </div>
  )
}
